import React,{useState} from "react";
import { useDropzone } from "react-dropzone";
import {Dialog,DialogTitle,DialogActions,DialogContent,Button } from "@material-ui/core";
function Dragndropsimage(props) {
const[showmessage,setshowmessage]=useState("");
const[imagee,setimagee]=useState(props.imagees1);
const imagees="https://nmdl.libnet.info/images/events/nmdl/upload1.jpg";
  const {
    acceptedFiles,
    fileRejections,
    getRootProps,
    getInputProps
  } = useDropzone({
    accept: 'image/jpeg, image/png'
  });
const acceptedFileItems = acceptedFiles.map(file => (
    <p key={file.path}>
      {file.path} - {file.size} bytes
    </p>
  ));

  const fileRejectionItems = fileRejections.map(({ file, errors }) => (
    <p key={file.path}>Not Accepted--
      {file.path} - {file.size} bytes
      <p>
        {errors.map(e => (
          <p key={e.code}>{e.message}</p>
        ))}
      </p>
    </p>
  ));
if(acceptedFileItems[0]){
    const reader= new FileReader();
    reader.readAsDataURL(acceptedFiles[0])
    reader.onload=async(event)=>{
    setimagee(event.target.result)
    }
}
const changeimage=()=>{
if(imagee!==imagees){
    props.setdragdrop(imagee)
    props.setshow1()
}else{
    setshowmessage("please select image")
}    
}
return (
<>
<Dialog open={props.show} style={{backgroundColor:"#061829"}}><DialogTitle><img className='my-1 mx-1 rounded-circle' src={imagee}  alt="" style ={{height:"5%",width:"5%",objectFit:"cover"}}/>Upload Image</DialogTitle>
<DialogContent dividers>
<div className="my-5 mx-5 border border-light text-center">
    <section className="container">
      <div {...getRootProps({ className: 'dropzone' })}>
        <input {...getInputProps()} />
        <img className='my-1 mx-1 ' src={imagee}  alt="" style ={{height:"25%",width:"25%",objectFit:"cover"}}/>
        <p>Drag and drop a image here</p>
        <p>or</p>
        <em>(Only *.jpeg , *.gif and *.png images will be accepted)</em>
        <Button className='bg-primary' onClick={()=>setshowmessage("") } style={{color:"black",backgroundColor:"primary",borderRadius:5}}>Choose file</Button><br/>
        <small>file choosen</small>
      </div>
      <aside>
        <span>{acceptedFileItems}</span>
        <span>{fileRejectionItems}</span>
        <span>{showmessage}</span>
      </aside>
    </section>
    </div>
    </DialogContent>
    <DialogActions>
    <Button  color='primary' onClick={changeimage}>Upload</Button>
    <Button  color='secondary' onClick={()=>props.setshow1()}>Cancel</Button>
    </DialogActions>
    </Dialog>
    </>
  );
};export default Dragndropsimage;
