import React, {  useState,useEffect } from "react";
import { useParams,useHistory } from "react-router-dom";
import {Dialog,DialogTitle,DialogContent,Button,Select,MenuItem } from "@material-ui/core";
import axios from "axios";
import { ToastContainer,toast } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";
import Dragndropsimage from "./Dragndropsimage";
import Dragndropsresume from "./Dragndropsresume";
const Editonestudent=()=>{
const imagees1="https://nmdl.libnet.info/images/events/nmdl/upload1.jpg";
const history= useHistory();
const newdate =new Date().toLocaleDateString();
let newtime =new Date().toLocaleTimeString();
const [timee,settimee]=useState(newtime);
const [valuee,setvaluee]=useState([]);
const [show,setshow]=useState(false);
const [showresume,setshowresume]=useState(false);
const {id}=useParams();
useEffect(()=>{dataget()},[]);
const dataget= async ()=>{
  const result = await axios.get(`http://localhost:3003/users/${id}`);
  setvaluee(result.data)
}
const {sname,fname,dob,add,city,state,pin,Pnum,Email1,classs,marks,date,time,images1,resume}=valuee;
const valuearray=[sname,fname,dob,add,city,state,pin,Pnum,Email1,marks,date,time];
const heading=["Name","Father Name","Age","Address","city","State","Pin","Phone No","Email","Marks","Submit Date","Submit Time"];
const names=["sname","fname","dob","add","city","state","pin","Pnum","Email1","marks","date","time"];
const types=["text","text","date","text","text","text","tel","tel","email","number","text","text"]
const patterns=["","","","","","","","","","","",""]
const disable1=["","","","","","","","","","","true","true"]
//const patterns=["[A-Za-z]{3,15}","[A-Za-z]{3,15}","","[A-Za-z]{8,25}","[A-Za-z]{3,8}","[A-Za-z]{2,15}","[0-9]{6}","[6789][0-9]{9}","[a-z0-9._%+-]+@[a-z0-9.-]+.[a-z]{2,4}$","[0-9]{2}"]
const titles=["Enter Your Name","Enter Your Father Name","Enter Your Age","Enter Your Address",
"Enter Your city","Enter Your State","Enter Your Pin","Enter Your Phone No","Enter email add....xyz@gmail.com",
"Enter Your Marks"];
const updatetime=()=>{
  newtime =new Date().toLocaleTimeString();
  settimee(newtime);
}
setInterval(updatetime);
const inputevent=(event)=>{
  const {value,name}=event.target;
  setvaluee({...valuee,[name]:value})
}
const onSumbit= async (e)=> {
let deletedata = window.confirm("DO YOU WANT TO UPDATE USER DATA")    
if(deletedata){
e.preventDefault();
await axios.put(`http://localhost:3003/users/${id}`,valuee);
toast.success("STUDENT DATA HAS UPDATED",{position:"top-center"})
history.push('/');
}else{
    
}
}
const imagedownload=()=>{
    const link=document.createElement("a")
    link.href=images1;
    link.setAttribute("download","imagenew.png");
    link.click();
}
const setdragdrop=(e)=>{
  setvaluee({...valuee,images1:e})
}
const setdragdrop1=(e)=>{
  setvaluee({...valuee,resume:e})
}
const setshow1=(e)=>{
  setshow(false)
}
const setshow1resume=(e)=>{
  setshowresume(false)
}
return ( 
<>
<Dialog open={true} style={{backgroundColor:"#061829"}}><DialogTitle><h1 ><img src={images1} className='my-1 mx-1 rounded-circle' alt="image not found" style ={{height:50,width:50,objectFit:"cover"}}/>UPDATE STUDENT DATA</h1>{newdate}&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;{timee}</DialogTitle>
<DialogContent dividers style={{backgroundColor:"#061829",color:"white"}}>
<form onSubmit={onSumbit} >
<img src={images1} className='my-1 mx-1' alt="image not found" style ={{height:"20%",width:"20%"}}/>&nbsp;&nbsp;&nbsp;&nbsp;<span className="font-weight-bold font-hight-bold" style={{textTransform:"uppercase",color:"red"}} >{sname}</span><br/>
<Button onClick={()=>setshow(true)} className='bg-primary' style={{color:"black",backgroundColor:"primary",borderRadius:5}}>Chose Image</Button><br/><br/>
{valuearray.map((currentkey,index)=>{
return (
<div key={index}>
<p>{heading[index]}</p>
<input title={titles[index]} type={types[index]}  style={{color:"black",backgroundColor:"white",borderRadius:5,width:"100%"}} placeholder={currentkey} name={names[index]}  onChange={inputevent} value={currentkey} checked autoCapitalize='off' disabled={disable1[index]}/><br/><br/>     
</div>
) })}
<p>Class</p>
 
<Select style={{color:"black",backgroundColor:"white",borderRadius:5,width:"100%"}} onChange={inputevent} value={classs} placeholder={classs} label="classs" name="classs" required>
<MenuItem value={"B.C.A"}>B.C.A</MenuItem>
<MenuItem value={"BTECH"}>BTECH</MenuItem>
<MenuItem value={"M.C.A"}>M.C.A</MenuItem>
<MenuItem value={"L.L.B"}>L.L.B</MenuItem>
<MenuItem value={"B.B.A"}>B.B.A</MenuItem>
<MenuItem value={"M.B.A"}>M.B.A</MenuItem>
</Select><br/><br/>
<button type="submit" style={{borderRadius:10}}><Button>CLICK ME</Button></button>
<Button onClick={()=>setshowresume(true)}  className='bg-primary' style={{color:"black",backgroundColor:"primary",borderRadius:5,marginLeft:15}}>Update Resume</Button>
<Button onClick={imagedownload} className='bg-primary' style={{color:"black",backgroundColor:"primary",borderRadius:5,marginLeft:15}}>Download Image</Button>
<Button onClick={()=>history.push("/viewsalldata")} className='bg-danger' style={{color:"white",backgroundColor:"primary",borderRadius:5,marginLeft:15}}>Cancel</Button>
</form>
<Dragndropsimage show={show} setshow1={setshow1} setdragdrop={setdragdrop} imagees1={imagees1} />
<Dragndropsresume showresume={showresume} setshow1resume={setshow1resume} setdragdrop1={setdragdrop1} imagees1={imagees1} />
</DialogContent>
</Dialog>
     <ToastContainer/>
    
    </>
 );
 };export default Editonestudent;

 

 
 