import React,{useState,useEffect} from "react";
import axios from "axios";
import { Box,Card,Button,Typography,CardActionArea,CardMedia } from "@material-ui/core";
import Slider from "react-slick";
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";
import { useHistory } from "react-router-dom";
const Blankdiv=()=>{return (<div/>)};
const Netflix=(p)=>{
const history=useHistory();
const [inputdata,setinputdata]=useState([]);
useEffect(()=>{dataget();},[])
const dataget=async()=>{
    const res2 = await axios.get("http://localhost:3003/photo");
    setinputdata(res2.data)
}
return(
    <>
    {
        (inputdata.length!==0)?(<div >
            <Slider initialSlide={5} slidesToScroll={5} speed={500} slidesToShow={1} autoplay autoplaySpeed={3000} infinite prevArrow={<Blankdiv/>} nextArrow={<Blankdiv/>}>
            {inputdata.map((item,i)=>{return(
                <Box p={0.5} pt={0.5} mt={8}>
                <Card style={{borderRadius:"0"}}>
                <CardActionArea>
                <CardMedia component="img" image={item.url} style={{height:170,objectFit:"fill"}}/>
                </CardActionArea>
                </Card>
                </Box>
            )})}
            </Slider>
            {inputdata.filter((val)=>{
                if(p.search===""){
                    return val;
                }else if(val.title.toLowerCase().includes(p.search.toLowerCase())||val.language.toLowerCase().includes(p.search.toLowerCase())){
                    return val;
                }return null;   
                }).map((item,i)=>{return(<div>

                <Typography variant="body1" style={{color:"white",marginLeft:5,marginTop:5,paddingLeft:0.5,textTransform:"uppercase"}}>{item.title}</Typography>
                <Slider initialSlide={5} slidesToScroll={5} speed={500} slidesToShow={5} infinite prevArrow={<Blankdiv/>} nextArrow={<Blankdiv/>}>
                {inputdata.map((item,i)=>{return(
                <Box p={0.5}>
                <Card style={{borderRadius:"0",backgroundColor:"black",color:"white"}}>
                <CardActionArea onDoubleClick={()=>{history.push(`/viewmovieinfo/view/${item.id}`)}}>
                <CardMedia component="img" image={item.url} style={{height:100,objectFit:"fill"}}/>
                <Typography variant="body2" className="text-center">{item.subtitle}&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;{item.postdate}</Typography>
                </CardActionArea>
                </Card>
                </Box>
            )})}
            </Slider>
            <div style={{textAlign:"right"}}>
            <Button style={{color:"#142ec2"}} onClick={()=>{history.push(`/aprime/view/${item.title}`)}}>see more</Button>
            </div>
            </div>
            )})

            }
            </div>):<div style={{color:"white",textAlign:"center",marginTop:"20%"}}><h1>Loading......</h1></div>
    }
    </>
)
};export default Netflix;