import React, {  useState,useEffect } from "react";
import { useParams,useHistory } from "react-router-dom";
import {Typography,Dialog,DialogTitle,DialogActions,DialogContent,Button,InputBase,Select,MenuItem } from "@material-ui/core";
import axios from "axios";
const Viewonestudent=()=>{
const history= useHistory();
const newdate =new Date().toLocaleDateString();
let newtime =new Date().toLocaleTimeString();
const [timee,settimee]=useState(newtime);
const [valuee,setvaluee]=useState([]);
const {id}=useParams();
useEffect(()=>{dataget()},[]);
  const dataget= async ()=>{
  const result = await axios.get(`http://localhost:3003/users/${id}`);
  setvaluee(result.data)
}
const {sname,fname,dob,add,city,state,pin,Pnum,Email1,classs,marks,date,time,images1,resume,language}=valuee;
const valuearray=[sname,fname,dob,add,city,state,pin,Pnum,Email1,classs,language,marks,date,time];
const heading=["Name","Father Name","Age","Address","city","State","Pin","Phone No","Email","classs","Known Language","Marks","Submit Date","Submit Time"];
const updatetime=()=>{
  newtime =new Date().toLocaleTimeString();
  settimee(newtime);
}
setInterval(updatetime);
const imagedownload=()=>{
  const link=document.createElement("a")
  link.href=images1;
  link.setAttribute("download","imagenew.png");
  link.click();
}
const imagedownload1=()=>{
  const link=document.createElement("a")
  link.href=resume;
  link.setAttribute("download","resume.doc");
  link.click();
}
 return ( 
    <>
    <Dialog open={true} style={{backgroundColor:"#061829"}}><DialogTitle><h1 > <img src={images1} className='my-1 mx-1 rounded-circle' alt="image not found" style ={{height:50,width:50,objectFit:"cover"}}/>VIEW A STUDENT</h1>{newdate}&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;{timee}</DialogTitle>
    <DialogContent dividers style={{backgroundColor:"#061829",color:"white"}}>
    <img src={images1} className='my-1 mx-1' alt="image not found" style ={{height:"20%",width:"20%"}}/>&nbsp;&nbsp;&nbsp;&nbsp;<span className="font-weight-bold font-hight-bold" style={{textTransform:"uppercase",color:"red"}} >{sname}</span>
    {valuearray.map((currentkey,index)=>{
    return (
    <div key={index}>
    <p>{heading[index]}</p>
    <input type="text" placeholder={currentkey} disabled style={{color:"black",backgroundColor:"white",borderRadius:5,width:"100%"}} /><br/><br/>
    </div>
      ) })}</DialogContent>
      
      <DialogActions>
      <Button onClick={imagedownload} className='bg-primary' style={{color:"black",backgroundColor:"primary",borderRadius:5,marginLeft:15}}>Download Image</Button>
      <Button onClick={imagedownload1} className='bg-primary' style={{color:"black",backgroundColor:"primary",borderRadius:5,marginLeft:15}}>Download Resume</Button>
      <Button onClick={()=>history.push("/viewsalldata")} className='bg-danger' style={{color:"white",backgroundColor:"primary",borderRadius:5,marginRight:15}}>Cancel</Button>
      </DialogActions>
      
      </Dialog>
    
    </>
 );
 };export default Viewonestudent;

 

 
 