import React,{ useState } from 'react';
import axios from "axios";
import Enterydata from './Enterydata';
import { useHistory } from "react-router";
import { ToastContainer,toast } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";
import {Typography,Dialog,DialogTitle,DialogActions,DialogContent,Button,InputBase,Select,MenuItem } from "@material-ui/core";
const Appschool = ()=>{
const history= useHistory();
const newdate =new Date().toLocaleDateString();
let newtime =new Date().toLocaleTimeString();
const [timee,settimee]=useState(newtime);
const [valuee,setvaluee]=useState(Enterydata);
const updatetime=()=>{
    newtime =new Date().toLocaleTimeString();
    settimee(newtime);
}
setInterval(updatetime);
const {id,sname,fname,dob,add,city,state,pin,Pnum,Email1,classs,marks,date,time,images1,resume,language}=valuee;
const valuearray=[sname,fname,dob,add,city,state,pin,Pnum,Email1,marks];
const names=["sname","fname","dob","add","city","state","pin","Pnum","Email1","marks"];
const heading=["Name","Father Name","Age","Address","city","State","Pin","Phone No","Email","Marks"];
const placeholders=["Enter Your Name","Enter Your Father Name","Enter Your Age","Enter Your Address",
"Enter Your city","Enter Your State","Enter Your Pin","Enter Your Phone No","Enter email add....xyz@gmail.com",
"Enter Your Marks"];
const types=["text","text","date","text","text","text","tel","tel","email","number"]
const patterns=["","","","","","","","","",""]
//const patterns=["[A-Za-z]{3,15}","[A-Za-z]{3,15}","","[A-Za-z]{8,25}","[A-Za-z]{3,8}","[A-Za-z]{2,15}","[0-9]{6}","[6789][0-9]{9}","[a-z0-9._%+-]+@[a-z0-9.-]+.[a-z]{2,4}$","[0-9]{2}"]
const titles=["Enter Your Name","Enter Your Father Name","Enter Your Age","Enter Your Address",
"Enter Your city","Enter Your State","Enter Your Pin","Enter Your Phone No","Enter email add....xyz@gmail.com",
"Enter Your Marks"];
const onSumbit= async (e)=>{
        e.preventDefault();
        await axios.post(`http://localhost:3003/users`,valuee);
        toast.success("Successful Login",{position:"top-center"})
        history.push('/');
         
 };
const inputevent=(event)=>{
    const {value,name}=event.target;
    setvaluee({
        ...valuee,
        [name]:value,
        time:timee,
        date:newdate
    });};
    const inputevent1=(event)=>{
        const reader= new FileReader();
        const {name,files}=event.target;
        reader.readAsDataURL(files[0])
        reader.onload=(event)=>{
        setvaluee({...valuee,[name]:event.target.result})
        }
    }


return(
<>
<Dialog open={true} style={{backgroundColor:"#061829"}}><DialogTitle><h1 >ENROLLMENT STUDENT</h1>{newdate}&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;{timee}</DialogTitle>
<DialogContent dividers style={{backgroundColor:"#061829",color:"white"}}>
<form onSubmit={onSumbit} >
<img src={images1} className='my-1 mx-1' alt="image not found" style ={{height:"20%",width:"20%"}}/>
<input className='custom-file-input' title="" type="file" pattern="" accept='image/*' style={{backgroundColor:"white",borderRadius:5,width:"100%",marginTop:5,color:"black"}}  name="images1"  placeholder="please insert an image file" onChange={inputevent1} checked autoCapitalize='off' required/><br/><br/>     
{valuearray.map((currentkey,index)=>{
return (
<div key={index} >

<p>{heading[index]}</p>

<input title={titles[index]} type={types[index]} style={{color:"black",backgroundColor:"white",borderRadius:5,width:"100%"}} placeholder={placeholders[index]} name={names[index]}  onChange={inputevent} value={currentkey} checked autoCapitalize='off' /><br/><br/>     

</div>
)})}

<p >Class</p>
 
<Select style={{color:"black",backgroundColor:"white",borderRadius:5,width:"100%"}} onChange={inputevent} value={classs} name="classs" required>
<MenuItem value={"B.C.A"}>B.C.A</MenuItem>
<MenuItem value={"BTECH"}>BTECH</MenuItem>
<MenuItem value={"M.C.A"}>M.C.A</MenuItem>
<MenuItem value={"L.L.B"}>L.L.B</MenuItem>
<MenuItem value={"B.B.A"}>B.B.A</MenuItem>
<MenuItem value={"M.B.A"}>M.B.A</MenuItem>
</Select><br/><br/>

<p >Known Language</p>
 
<Select style={{color:"black",backgroundColor:"white",borderRadius:5,width:"100%"}} onChange={inputevent} value={language} name="language" required>
<MenuItem value={"HINDI"}>HINDI</MenuItem>
<MenuItem value={"ENGLISH"}>ENGLISH</MenuItem>
</Select><br/><br/>

<p>Select Reasume</p>
<input className='custom-file-input' title="" type="file" pattern="" accept='' style={{backgroundColor:"white",borderRadius:5,width:"100%",marginTop:5,color:"black"}}  name="resume"  placeholder="please select a Resume" onChange={inputevent1} checked autoCapitalize='off' required/><br/><br/>
<button type="submit" style={{borderRadius:10}}><Button>CLICK ME</Button></button>
<Button onClick={()=>history.push("/viewsalldata")} className='bg-danger' style={{color:"white",backgroundColor:"primary",borderRadius:5,marginLeft:15}}>Cancel</Button>
</form>
</DialogContent>
</Dialog>
<ToastContainer/>
</>
);
}; export default Appschool;



