import React ,{useState} from "react";
import { ToastContainer,toast } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";
import { useHistory } from "react-router";
import axios from "axios";
import { Link } from "react-router-dom";
const Data={
    id:"",
    email:"",
    password:"",
}
const Netsignup1=()=>{
    const history= useHistory();
    const[inputdata,setinputdata]=useState(Data);
    const[error,setError]=useState("");
    const{email,password}=inputdata;
    const valuearray=[email,password];
    const names=["email","password"];
    const placeholders=["email@example.com","*****************"]
    const heading=["EMAIL","PASSWORD"]
    const types=["text","password"]
   // const patterns=["",""]
    const patterns=["[a-z0-9._%+-]+@[a-z0-9.-]+.[a-z]{2,4}$","^(?=.*[a-z])(?=.*[A-Z])(?=.*[0-9])(?=.*[!@#$%^&*_=+-]).{6,12}$"]
    const titles=["PLEASE ENTER VALID EMAIL ADDRESS","PASSWORD LENGTH MUST BE 6-12 CHAR AND AT LEAST 1 NUMBER LEAST 1 CAPTICAL AND LEAST 1 SPECIAL CHAR"]
    const inputvalue=(e)=>{
        let {name,value}=e.target
        setinputdata({...inputdata,[name]:value})
    }
    const submitData=(e)=>{
        e.preventDefault();
        axios.post("http://localhost:3002/api/auth/register",{
            email,
            password,
        })
        .then((response)=>{
            console.log("response",response);
            localStorage.setItem(
                "login",
                JSON.stringify({
                    userLogin:true,
                    token:response.data.access_token,
                })
                );
                setError("");
                toast.success("Successful Signup",{position:"top-center"})
                history.push('/home');
            })
            .catch((error)=>{setError(error.response.data.massage);
                toast.success("Email Id & Password already exist",{position:"top-center"})
            });
        setinputdata(Data)
    }
    return(
        <>
        <form onSubmit={submitData} action="adduser">
        <div className="card shadow my-5 mx-5 border border-dark">
        <div className="w-25 mx-auto my-5 border border-light">
        <h1 className="text-center">Signup</h1>
        <p style={{color:"red"}}>{error}</p>
        <hr className="mb-5"/>
        {valuearray.map((currentitem,index)=>{
            return(
                <div key={index}>
                <p className="mt-2">{heading[index]}</p>
                <input style={{borderRadius:5,width:"100%"}} pattern={patterns[index]} title={titles[index]} type={types[index]} name={names[index]} placeholder={placeholders[index]} value={currentitem} onChange={inputvalue} checked autoComplete="off" required />
                </div>
            )})}
            <button className="btn btn-warning bg-danger text-white mt-4 text-center" id="adduser">Signup</button>
        </div>
        <p style={{textAlign:"center",color:"red"}}>Already have an account then please<Link to="/login"> Login </Link></p>
        </div>
        </form>
        <ToastContainer/>
        </>
    )
};export default Netsignup1;
