import React,{useState,useEffect} from "react";
import axios from "axios";
import { Box,Grid, Card,Button,Typography,CardActionArea,CardMedia } from "@material-ui/core";
import Slider from "react-slick";
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";
import { useHistory } from "react-router-dom";
import { useParams } from "react-router-dom";
const Blankdiv=()=>{return (<div/>)};
const Aprime=(p)=>{
const history=useHistory();
const [visible1,setvisible1]=useState("Oldest")
const {list}=useParams();
const [inputdata,setinputdata]=useState([]);
const [order,setorder]=useState("ASC")
useEffect(()=>{dataget();},[])
const dataget=async()=>{
    const res2 = await axios.get("http://localhost:3003/photo");
    setinputdata(res2.data)
}
const sortfun=(col)=>{
    if(order==="ASC"){
        const sorted=[...inputdata].sort((a,b)=>
        new Date(b[col])-new Date(a[col])
       // a[col].Split("/").reverse().pop().toLowerCase()>b[col].split("/").reverse().pop().toLowerCase()?1:-1
        );setinputdata(sorted);
        setorder("DSC")
    }
    if(order==="DSC"){
        const sorted=[...inputdata].sort((a,b)=>
        new Date(a[col])-new Date(b[col])
       // a[col].Split("/").reverse().pop().toLowerCase()<b[col].split("/").reverse().pop().toLowerCase()?1:-1
        );setinputdata(sorted);
        setorder("ASC")
    }
}
return(
    <>
    {
        (inputdata.length!==0)?(<div>
            <div>
            <Slider initialSlide={5} slidesToScroll={5} speed={500} slidesToShow={1} autoplay autoplaySpeed={3000} infinite prevArrow={<Blankdiv/>} nextArrow={<Blankdiv/>}>
            {inputdata.map((item,i)=>{return(
                <Box p={0.5} pt={0.5} mt={8}>
                <Card style={{borderRadius:"0"}}>
                <CardActionArea>
                <CardMedia component="img" image={item.url} style={{height:170,objectFit:"fill"}}/>
                </CardActionArea>
                </Card>
                </Box>
            )})}
            </Slider>
            </div>
            <Typography variant="h6" style={{color:"white",marginTop:10,marginLeft:5}}>{list}<Button style={{color:"red",backgroundColor:"black",marginLeft:5}} onClick={()=>{sortfun("postdate");((visible1==="Oldest")?setvisible1("Latest"):setvisible1("Oldest"))}}>{(visible1==="Oldest")?"Latest":"Oldest"}</Button></Typography>
            <Grid container spacing={2}>
            {inputdata.filter((val)=>{
                if(p.search===""){
                    return val;
                }else if(val.subtitle.toLowerCase().includes(p.search.toLowerCase())||val.language.toLowerCase().includes(p.search.toLowerCase())){
                    return val;
                }return null;   
                }).map((item,i)=>{return(
                <Grid item sm={4}>
                <Card style={{borderRadius:"0",backgroundColor:"black",color:"white"}} className="text-center mt-4 me-1">
                <CardActionArea onDoubleClick={()=>{history.push(`/viewmovieinfo/view/${item.id}`)}}>
                <CardMedia component="img" image={item.url} style={{height:100,objectFit:"fill"}}/>
                <Typography variant="body2" className="text-center">{item.subtitle}&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;{item.postdate}</Typography>
                </CardActionArea>
                </Card></Grid>
             
            )})}
            </Grid>
          
            </div>):<div style={{color:"white",textAlign:"center",marginTop:"20%"}}><h1>Loading......</h1></div>
    }
    </>
)
};export default Aprime;