import React,{useState,useEffect} from "react";
import axios from "axios";
import { Box,Grid, Card,Button,Typography,CardActionArea,CardMedia,CardContent,CardActions,CardHeader } from "@material-ui/core";
import { useHistory } from "react-router-dom";
import { useParams } from "react-router-dom";
const Viewmovieinfo=(p)=>{
const history=useHistory();
const {list}=useParams();
const [inputdata,setinputdata]=useState([]);
useEffect(()=>{dataget();},[])
const dataget=async()=>{
    const res2 = await axios.get(`http://localhost:3003/photo/${list}`);
    setinputdata(res2.data)
}
return(
    <>
    {(inputdata.length!==0)?(
    <Box mt={10}>
    <Grid container>
    <Grid  sm={6}>
    
    
            
            
                <Card style={{borderRadius:"0",backgroundColor:"#4e5357",color:"black",height:"100%"}} className="mt-4 me-1">
                <CardHeader title={inputdata.title} subheader={inputdata.postdate}/>
                <CardContent>
                <Typography variant="h4">{inputdata.movieinfo}</Typography>
                </CardContent>
                <CardActions>
                <Button  style={{backgroundColor:"#011222",color:"white"}} onDoubleClick={()=>{history.push(`/watchNow`)}}>Watch Now</Button>
                </CardActions>
                </Card>
           
    </Grid> 
    <Grid  sm={6}>  
            
            
    <Card style={{borderRadius:"0",backgroundColor:"#4e5357",color:"black",height:"100%"}} className="mt-4 me-1">
    
    <Typography variant="h6" align="center" className="mt-4">{inputdata.subtitle}</Typography>
   
    <CardActionArea onDoubleClick={()=>{history.push(`/watchNow`)}} >
    <CardMedia component="img" image={inputdata.url} />
    </CardActionArea>
    </Card>
             
            
            </Grid>
          
            </Grid> </Box>):<div style={{color:"white",textAlign:"center",marginTop:"20%"}}><h1>Loading......</h1></div>
    }
    
    </>
)
};export default Viewmovieinfo;