import React from "react";
import { useHistory } from "react-router";
import axios from "axios";
import { Button,Typography,AppBar,Toolbar,InputBase,Select,MenuItem } from "@material-ui/core";
const MainHeader=(p)=>{
const history= useHistory();
return ( 
 <>
 <AppBar>
<Toolbar style={{backgroundColor:"#011222"}}> 
<Typography variant="h5" style={{color:"red",flexGrow:1}}>Netflix<span style={{marginLeft:15,color:"white"}}>{p.nametitle}</span></Typography>
<InputBase style={{backgroundColor:"#055496",color:"black",borderRadius:5}} name="search" placeholder="Search" autoComplete="off" value={p.search} onChange={p.inputevent}></InputBase>
<Button style={{color:"#85a2be"}}  onClick={()=>history.push('/home')}>Home</Button>
<Button style={{color:"#85a2be"}}  onClick={()=>history.push('/login')}>Login</Button>
<Button style={{color:"#85a2be"}}  onClick={()=>history.push('/viewsalldata')}>STUDENT DATA</Button>
<Button style={{color:"#85a2be"}}  onClick={()=>history.goBack()}>GO BACK</Button>
<Button style={{color:"#85a2be"}}  onClick={()=>{ localStorage.setItem("login",JSON.stringify({userLogin:false,token:"", email_id:"",}));return(history.push('/login'))}}>Logout</Button>
<Select label="Class" style={{backgroundColor:"#055496",borderRadius:5,width:"10%"}} onChange={(e)=>p.inputevent(e)}>
<MenuItem value={"HINDI"}>HINDI</MenuItem>
<MenuItem value={"ENGLISH"}>ENGLISH</MenuItem>
</Select><br/><br/>

</Toolbar>
</AppBar>
</>
 );
 };export default MainHeader;

 

 
 