import React, {  useState } from "react";
import { Box,Card,Button,Typography,CardActionArea,CardMedia } from "@material-ui/core";
import { useHistory } from "react-router";
import axios from "axios";
import { useEffect } from 'react';
const ViewsAllData=(p)=>{
const history= useHistory();
const [valuee,setvaluee]=useState([]);
const [visible1,setvisible1]=useState("ASC")
const [order,setorder]=useState("ASC")
useEffect(()=>{dataget();},[]);
const sortfun=(col)=>{
  if(order==="ASC"){
      const sorted=[...valuee].sort((a,b)=>
      a[col].toLowerCase()>b[col].toLowerCase()?1:-1
      );setvaluee(sorted);
      setorder("DSC")
  }
  if(order==="DSC"){
      const sorted=[...valuee].sort((a,b)=>
      a[col].toLowerCase()<b[col].toLowerCase()?1:-1
      );setvaluee(sorted);
      setorder("ASC")
  }
}
const dataget= async ()=>{
const result = await axios.get("http://localhost:3003/users");
setvaluee(result.data)
}
const removeelement= async (id)=>{
  await axios.delete(`http://localhost:3003/users/${id}`,valuee);
  dataget();
}
const edit=(id,time)=>{
history.push(`/editonestudent/edit/${id}/${time}`);
}
const view=(id)=>{
history.push('/Viewonedata/view/'+id);
}
console.log(p.search1)
return ( 
      <>
      {(valuee.length!==0)?(<div>
      <div className="container-fluid mt-5" style={{backgroundColor:"#061829",color:"white"}}>
      <div className="main-heading">
      <h1 className="me-5 text-center" ><span className="font-weight-bold">STUDENT DATA</span></h1>
      <Button style={{backgroundColor:"black",color:"white"}} onClick={()=>history.push('/appschool')}>NEW STUDENT</Button><Button style={{color:"white",backgroundColor:"black",marginLeft:5}} onClick={()=>{sortfun("sname");((visible1==="ASC")?setvisible1("DSC"):setvisible1("ASC"))}}>{(visible1==="ASC")?"DSC":"ASC"}</Button>
      </div>
      <div className="table-responsive">
      <table className="table table-hover" style={{color:"white"}}>
      <thead >
      <tr>
      <th>S.NO.</th>
      <th>Student Name</th>
      <th>Email</th>
      <th>Phone No.</th>
      <th>Known Language</th>
      <th>Marks%</th>
      <th>View</th>
      <th >Edit</th>
      <th >Delete</th>

      </tr>
      </thead>
      
      
      
    
     {valuee.filter((val)=>{
      if(p.search1===""){
        return val;
      }else if(val.sname.toLowerCase().includes(p.search1.toLowerCase())||val.Pnum.toLowerCase().includes(p.search1.toLowerCase())||val.language.toLowerCase().includes(p.search1.toLowerCase())){
       return val;
      }return null;
    } ).map((currentkey,index)=>{
     return (
    
    <tbody key={index}>
      <tr>
      <th>{index+1}</th>
      <th> {currentkey.sname}</th>
      <th>{currentkey.Email1}</th>
      <th>{currentkey.Pnum}</th>
      <th>{currentkey.language}</th>
      <th>{currentkey.marks}%</th>
      <th ><Button style={{backgroundColor:"white",color:"black"}}  onClick={()=>view(currentkey.id)}>View</Button></th>
      <th ><Button style={{backgroundColor:"white",color:"black"}} onClick={()=>edit(currentkey.id,currentkey.time)} >Edit</Button></th>
      <th ><Button style={{backgroundColor:"white",color:"black"}} onClick={()=>removeelement(currentkey.id)} >Delete</Button></th>
      
      </tr>
      </tbody>

    )
  })}
    
    </table>
    </div>
</div>
</div>):<div style={{color:"white",textAlign:"center",marginTop:"20%"}}><h1>Loading......</h1></div>}
</>
  )
  };export default ViewsAllData;