import { Container } from "@material-ui/core";
import { makeStyles } from "@material-ui/core";
import React, {  useState } from "react";
import { Route,Switch } from "react-router-dom";
import MainHeader from "./MainHeader";
import ViewsAllData from "./ViewsAllData";
import Appschool from './Appschool';
import Viewonedata from './Viewonedata';
import Editonestudent from './Editonestudent';
import Netflix from './Netflix';
import Aprime from './Aprime';
import Viewmovieinfo from './Viewmovieinfo';
import WatchNow from './WatchNow';
import Netlogin from './Netlogin';
import Netsignup1 from './Netsignup1';

const useStyle=makeStyles({
    root:{
        paddingTop:10,
        backgroundColor:"#011222"
    },
})
const Mainroutes=()=>{
const classes=useStyle();
const [search,setsearch]=useState("")
const [nametitle,setnametitle]=useState("hh")
//localStorage.setItem("login",JSON.stringify({userLogin:false,token:"", email_id:"",}))
// const [isloginTrue,setisloginTrue]=useState({
//     userLogin:false,
//     token:"",
// })
//const isloginTrue=JSON.parse(localStorage.getItem("login"))
//console.log(isloginTrue)
const getlocalstroagevalue=()=>{
    const isloginTrue=JSON.parse(localStorage.getItem("login"))
    isloginTrue?console.log("table hai") :localStorage.setItem("login",JSON.stringify({userLogin:false,token:"", email_id:"",}))
    
}
const inputevent=(e)=>{
    setsearch(e.target.value);
}
const blanktextandselect=(e)=>{
    setsearch("");
}
return ( 
<>
<Container className={classes.root}>
<MainHeader inputevent={inputevent} search={search} blanktextandselect={blanktextandselect} nametitle={nametitle} />
<Switch>
<Route exact path='/' render={()=>{getlocalstroagevalue();const isloginTrue=JSON.parse(localStorage.getItem("login"));setnametitle(isloginTrue.email_id);return(isloginTrue&&isloginTrue.userLogin?<Netflix search={search}/>:<Netlogin/>)}}/>
<Route exact path='/home' render={()=>{const isloginTrue=JSON.parse(localStorage.getItem("login"));setnametitle(isloginTrue.email_id);return(isloginTrue&&isloginTrue.userLogin?<Netflix search={search}/>:<Netlogin/>)}}/>
<Route exact path='/viewsalldata' render={()=>{const isloginTrue=JSON.parse(localStorage.getItem("login"));return(isloginTrue&&isloginTrue.userLogin?<ViewsAllData search1={search} search={search}/>:<Netlogin/>)}}/>
<Route exact path='/appschool' render={()=>{const isloginTrue=JSON.parse(localStorage.getItem("login"));return(isloginTrue&&isloginTrue.userLogin?<Appschool/>:<Netlogin/>)}}/>
<Route exact path='/aprime/view/:list' render={()=>{const isloginTrue=JSON.parse(localStorage.getItem("login"));return(isloginTrue&&isloginTrue.userLogin?<Aprime search={search}/>:<Netlogin/>)}}/>
<Route exact path='/viewmovieinfo/view/:list' render={()=>{const isloginTrue=JSON.parse(localStorage.getItem("login"));return(isloginTrue&&isloginTrue.userLogin?<Viewmovieinfo/>:<Netlogin/>)}}/>
<Route exact path='/watchNow' render={()=>{const isloginTrue=JSON.parse(localStorage.getItem("login"));return(isloginTrue&&isloginTrue.userLogin?<WatchNow/>:<Netlogin/>)}}/>
<Route exact path='/Viewonedata/view/:id' render={()=>{const isloginTrue=JSON.parse(localStorage.getItem("login"));return(isloginTrue&&isloginTrue.userLogin?<Viewonedata/>:<Netlogin/>)}}/>
<Route exact path='/editonestudent/edit/:id/:time' render={()=>{const isloginTrue=JSON.parse(localStorage.getItem("login"));return(isloginTrue&&isloginTrue.userLogin?<Editonestudent/>:<Netlogin/>)}}/>
<Route exact path='/login' render={()=>{const isloginTrue=JSON.parse(localStorage.getItem("login"));setnametitle("");return(<Netlogin/>)}}/>
<Route exact path='/netsignup1' render={()=><Netsignup1/>}/>
</Switch>
</Container>
 </>
 );
 };export default Mainroutes;
